<template>
  <div class="mine">
    <div class="banner">
      <j-header bg="none" title="个人中心" icon="setting" icon-router=""></j-header>
      <div class="icon-box">
        <img :src="params.picture" alt="">
        <p>{{ params.username }}</p>
      </div>
      <nav>
        <p><span>{{ params.salesman*1 === 10 ? params.wallet : params.balance }}</span><span>可用余额（元）</span></p>
        <p><span>0.00</span><span>可用赢家券（元）</span></p>
      </nav>
    </div>
    <main>
      <div class="mine-btn">
        <router-link tag="button" :to="{ name: 'recharge' }">充 值</router-link>
        <router-link tag="button" :to="{ name: 'withdraw' }">提 现</router-link>
      </div>
      <ul class="list">
        <li v-waves v-for="(obj, index) in list" :key='index' class="wave" @click="routerPush(obj.router)">
          <svg-icon :icon-class="obj.icon"></svg-icon>
          <span>{{ obj.title }}</span>
        </li>
      </ul>
    </main>
  </div>
</template>

<script>
// @ is an alias to /src
import JHeader from '@/components/JHeader'
import { mapActions } from 'vuex'
// import waves from '@/directive/waves/index.js' // 水波纹指令

export default {
  name: 'mine',
  components: {
    JHeader
  },
  // directives: {
  //   waves
  // },
  data () {
    return {
      params: {},
      list: [
        {
          icon: 'ticket',
          title: '赢家券',
          router: 'winvoucher'
        },
        {
          icon: 'holdquery',
          title: '平仓查询',
          router: 'inquiries'
        },
        {
          icon: 'detail',
          title: '收支明细',
          router: 'accountrecord'
        },
        {
          icon: 'activity',
          title: '活动中心',
          router: 'activitycenter'
        },
        {
          icon: 'rule',
          title: '规则介绍',
          router: 'rule'
        },
        {
          icon: 'history',
          title: '历史公告',
          router: 'historynotice'
        },
        {
          icon: 'bank',
          title: '银行卡',
          router: 'bank'
        },
        {
          icon: 'agent',
          title: '经纪人',
          router: 'agent'
        },
        {
          icon: 'reset',
          title: '密码修改',
          router: 'moditypwd'
        },
        {
          icon: 'introduce',
          title: '玩法指引',
          router: 'guide'
        },
        {
          icon: 'exit',
          title: '退出登录',
          router: 'login'
        }
      ]
    }
  },
  beforeRouteLeave (to, from, next) {
    if (to.name === 'inquiries') {
      to.meta.keepAlive = false
    }
    next()
  },
  created () {
    this.init()
  },
  methods: {
    ...mapActions(['GetUserInfo']),
    init () {
      this.GetUserInfo().then(res => {
        res.code === 1 && (this.params = res.data)
      })
    },
    routerPush (route) {
      if (route === 'login') {
        this.logOut()
      } else if (route === 'agent') {
        if (this.params.state * 1 === 2) {
          this.$router.push({ name: route })
        } else if (this.params.state * 1 === 1) {
          this.$confirm({
            message: '正在审核，请稍后再试。',
            cancelButtonShow: false
          })
        } else {
          this.$router.push({ name: 'applicant' })
        }
      } else {
        this.$router.push({ name: route })
      }
      // route === 'login' ? this.logOut() : this.$router.push({ name: route })
    },
    logOut () {
      this.$confirm('您确定要退出登录吗？').then(() => {
        this.$store.dispatch('LogOut')
        this.$toast('请重新登录')
      }).catch(() => {
        this.$toast({
          position: 'top',
          message: '已取消操作'
        })
      })
    }
  },
  mounted () {
  }
}
</script>

<style lang="scss" scoped>
  @import './index.scss';
</style>
